
let html = document.querySelector("#html");
let style = document.querySelector("#style");
let string = `
 /* 你好，我叫徐野,字退之
  * 接下来我来演示一下我的前端功底
  * 首先我要准备一个div
  **/
#div1{
    border: 1px solid black;
    width:200px;
    height:200px;
}
/* 接下来我把div变成一个八卦图
 * 注意看好了
 * 首先，把div变成一个圆
 **/
 #div1{
    border-radius:50%;
    box-shadow: 0 0 3px rgba(0,0,0,0.5);
    border:none;

 }
 /* 八卦是阴阳形成的
  * 一黑一白
  * 一上一下
  **/
 #div1{
    background: linear-gradient(90deg, 
    rgba(255,255,255,1) 50%, 
    rgba(255,255,255,1) 50%,
    rgba(0,0,0,1) 50%, rgba(0,0,0,1) 100%);
}
/* 加两个神秘的能力法球*/
#div1::before{
    width:100px;
    height:100px;
    
    left:50%;
    top:0;
    transform:translateX(-50%);
    background:#000;
    border-radius:50%;
    background: radial-gradient(circle, 
        rgba(255,255,255,1) 0%, rgba(255,255,255,1) 24%, 
        rgba(255,255,255,1) 24%, rgba(255,255,255,1) 24%, 
        rgba(255,255,255,1) 24%, rgba(0,0,0,1) 24%);
}
#div1::after{
    width:100px;
    height:100px;
   
    left:50%;
    bottom:0;
    transform:translateX(-50%);
    background:#fff;
    border-radius: 50%;
    background: radial-gradient(circle, 
        rgba(0,0,0,1) 24%, rgba(255,255,255,1) 24%, 
        rgba(255,255,255,1) 100%, rgba(255,255,255,1) 100%, 
        rgba(0,0,0,1) 100%, rgba(255,255,255,1) 100%);
}
/ *  就这些了
  * 谢谢你的观看
  * 我们有缘再见
  **/
 
`;
let string2 = "";
let n = 0;
let step = () => {
    setTimeout(() => {

        if (string[n] === "\n") {
            string2 += "<br>";
        } else if (string[n] === " ") {
            string2 += " &nbsp";
        } else {
            string2 += string[n];
        }
        html.innerHTML = string2;
        style.innerHTML = string.substring(0, n);
        window.scrollTo(0,9999); //可以自动拖拽着屏幕向下
        html.scrollTo(0,9999);

        if (n < string.length - 1) {
            n += 1;
            step();
        }

    }, 30);
}
step();

